// body
import Vue from "vue";

export default function create(Component, props){
    //0. vue 
    const vm = new Vue({
        render(h){
            // render hVNodeDOM
            return h(Component, {props})
        }
    }).$mount();
    //$mount("#app")#app 
    //$mount https://cn.vuejs.org/v2/api/#vm-mount
    //1. vm 
    //2. $children
    // console.log(vm.$root)
    const comp = vm.$children[0];
    // 3. body
    document.body.appendChild(vm.$el);
    // 4. 
    comp.remove = ()=>{
        document.body.removeChild(vm.$el);
        vm.$destroy();
    }
    // 5. 
    return comp;
}